.container {
    box-sizing: border-box;
    padding-right: 10px;
    padding-left: 10px;
    margin-right: auto;
    margin-left: auto;
    position: relative;
}

.container {
    @extend %clearfix;
}


@media (min-width: 1200px) {
    .container {
        width: 1160px;
    }
}
